
import { makeStyles } from '@material-ui/core/styles';
import React, { useCallback, useEffect, useRef, useState } from "react";
import { AppBar, Toolbar, IconButton, Typography, Grid, Hidden, Paper, Divider, Box } from '@material-ui/core';
import CommonStyle from '../../styles/CommonStyle';

import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { dracula } from 'react-syntax-highlighter/dist/esm/styles/prism'

/* Use `…/dist/cjs/…` if you’re not in ESM! */
import { dark } from 'react-syntax-highlighter/dist/esm/styles/prism'
import { render } from 'react-dom'


import Comment from "../../containers/Comment"
import Title from './Title';
import useDeviceDetect from '../../hooks/useDeviceDetect';


const useStyles = makeStyles((theme) => ({
    contentPaper: {
        padding: theme.spacing(2),
        margin: theme.spacing(1),
    }
}));


const renderers = {
    code: ({ language, value }) => {
        return <SyntaxHighlighter style={dracula} language={language} children={value} />
    }
}

/**
 * @name renderers
 * @desc 마크다운 내 코드에 하이라이트 적용
*/
const components = {
    code({ node, inline, className, children, ...props }) {
        const match = /language-(\w+)/.exec(className || '')
        return !inline && match ? (
            <SyntaxHighlighter style={dark} language={match[1]} PreTag="div" children={String(children).replace(/\n$/, '')} {...props} />
        ) : (
            <code className={className} {...props} />
        )
    }
}

const RenderedContent = ({ content, type }) => {
    const { isMobile } = useDeviceDetect();
    const classes = useStyles({ isMobile });

    return (

        <Paper elevation={2} className={classes.contentPaper}>
            <Box p={isMobile ? 0 : 3} style={{fontSize:isMobile?"85%":"100%"}}>
                <ReactMarkdown renderers={renderers} components={components} children={content} />
            </Box>
        </Paper>
    )
}

export default RenderedContent;